/* Centrar el contenido y añadir estilos a la imagen */
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero-image {
  max-width: 100%;
  height: auto;
  margin-top: 20px;
  border-radius: 10px;
}

.cta-buttons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.register-btn {
  background-color: #004080;
  color: white !important;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  text-transform: uppercase;
  transition: background-color 0.35s ease;
  cursor: pointer;
  margin-right: 10px;
}

.register-btn a {
  color: white !important;
  text-decoration: none;
}

.register-btn:hover {
  background-color: #003366;
}

.cta-btn {
  display: inline-block;
  background-color: #004080;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.1rem;
  margin-top: 20px;
}





